import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import HeadingPrimary from "../../../components/HeadingPrimary";
import { rwd } from "../../../utils/rwd";

const StepFormHeader = () => {
  return (
    <Box py={3.9} borderBottom={1} borderColor={"info.light"}>
      <Typography variant="h1" color="info.dark" mb={4.2}>
        Deklaracja Przystpienia do<br />
        OZZIP Amazon
      </Typography>

      <HeadingPrimary
        headingText={
          "Przekazane dane s wycznie do wiadomoci OZZIP Amazon"
        }
      />

      <Typography
        variant="fs_12_sb"
        component="p"
        color="info.dark"
        sx={{ mt: rwd(3.9, 2) }}
      >
        DEKLARACJA
      </Typography>
      <Typography variant="fs_12_rg" color="info.dark">
        Ja, niej wskazany/wskazana owiadczam, i przystpuj do
        Oglnopolskiego Zwizku Zawodowego Inicjatywa Pracownicza z siedzib w
        Poznaniu (KRS nr 0000215247).
      </Typography>
    </Box>
  );
};

export default StepFormHeader;
