import Typography from "@mui/material/Typography";

const StepSummaryClauseConfirmation = () => {
  return (
    <Typography
      variant="fs_12_rg"
      component="p"
      color="info.dark"
      sx={{
        textAlign: "center",
        my: 1,
        maxWidth: 300,
        marginInline: "auto",
        border: 1,
        p: 1,
      }}
    >
      Zapoznaem/-am si z klauzul informacyjn i wyraam zgod na
      przetwaanie moich danych osobowych na zasadach i w celach w niej
      wskazanych.
    </Typography>
  );
};

export default StepSummaryClauseConfirmation;
