import Typography from "@mui/material/Typography";
import { useTheme } from "@mui/material/styles";

const StepConfirmationParagraphs = () => {
  const theme = useTheme();

  const typographyProps = {
    variant: theme.typography.fs_16_rg.variant,
    color: "info.dark",
    component: "p",
  };

  return (
    <article>
      <Typography {...typographyProps} sx={{ my: 3 }}>
        Dat przystpienia do zwizku osoby, ktra owiadczy przy uyciu rodkw
        komunikacji elektronicznej wol wstpienia do zwizku jest:
      </Typography>
      <Typography {...typographyProps} sx={{ ml: 3, my: 1.6 }}>
        <strong> a) </strong> ostatni dzie miesica, w ktrym owiadczenie to
        zostao wprowadzone do systemu teleinformatycznego w taki sposb by
        czonkowie prezydium organizacji podstawowych lub osoby dziaajce w
        imieniu prezydium mogy si z nimi zapozna,
      </Typography>
      <Typography {...typographyProps} sx={{ ml: 3, my: 1.6 }}>
        <strong>b)</strong> dzie, w ktrym po wprowadzeniu tego owiadczenia (w
        sposb wskazany w lit. a) prezydium podstawowej jednostki organizacyjnej
        zwizku podjo si jej obrony wobec podmiotu zatrudniajcego (w tym
        pracodawcy).
      </Typography>
    </article>
  );
};

export default StepConfirmationParagraphs;
